import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Col, Row, Button } from 'react-bootstrap';
import { getOpenListings } from './apiHelper';
import ListingCard from './components/ListingCard';
import { Listing } from './models/models';

export interface IWebsiteProps {

}
export interface IWebsiteState {
  listings: Listing[];

}
class App extends React.Component<IWebsiteProps, IWebsiteState> {

  constructor(props: IWebsiteProps) {
    super(props);
    this.state = { listings: [] };
  }






  private handleScroll = () => {

  };

  async componentDidMount() {

    const listingsResponse = await getOpenListings();

    this.setState({ listings: listingsResponse });
  }



  private listings = () => {
    if (this.state.listings.length === 0) return <span></span>;
    return (
      <Row>
        {
          this.state.listings.map(function (listing, i) {
            return (<ListingCard Listing={listing} key={i}></ListingCard>);
          })
        }
      </Row>

    );
  };



  public render(): React.ReactElement<{}> {
    return (
      <div className="App" >
        <h1>Dimba AS</h1>
          <section className="Listing">{this.listings()}</section>
      </div>
    );
  }
}

export default App;

